.open-button {
  background-color: #f21;
  color: white;
  padding: 16px 20px;
  border-radius: 50%;
  border: none;
  cursor: pointer;
  opacity: 0.8;
  position: fixed;
  bottom: 23px;
  right: 28px;
  width: 56px;
}

/* The popup chat - hidden by default */
.chat-popup {
  display: block;
  position: fixed;
  bottom: 0;
  right: 15px;
  border: 3px solid #f1f1f1;
  z-index: 9;
}

.reservation-img img{
  width: 300px;
  padding: 0px 6px 6px 0px;
  background-color:#991146;
  border-top-width: 8px;
  border-top-style:ridge;
  border-right-width: 5px;
  border-bottom-width: 5px;
  border-left-width: 2px;
  border-style: solid;
  border-color: #991146;
  height: 400px;
  width:70%;
  border: 1px solid;
  margin-top: 90px;

}
.reservation-img{
  display: flex;
  justify-content: center;
}
